body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.frameBody{
    margin-right: 15px;
    border:  1px solid black;
}
.frameMain{
    width: 68%;
    margin-left: 1%; 
    margin-right: 1%; 
    background-color: white;
}
.frameList{
    width: 28%;
    margin-left: 1%; 
    margin-right: 1%; 
    background-color: white;
}
.frameSearch{
    width: 40%;
    height: 20px;
    border-radius: 10px;
    border: 1px solid black;
    outline: none;
    padding-left: 15px;
    font-weight: bold
}
.frameMainList{
    flex-direction: row;
    display: flex;
}
.frameButton{
    border-radius: 10px;
    border: 1px solid black;
    width: 10%;
    height: 20px;
    margin-left: 10px;
    outline: none;
}
button.frameButton:hover{
    background-color: orange;
    color: white;
    font-weight: bold
}
.video{
    height: 500px; 
    width: 100% 
}
.videoList{
    height: 100px; 
    width: 100px 
}
.frameContentList{
    height: 500px;
    overflow-y: auto;
}
.frameName{
    margin-left: 8px;
}
